  async submit() {
    // validamos el campo personalNIT aquí antes de enviar los datos al servidor
    const regex = /^[a-zA-Z0-9]+$/;
    if (!regex.test(this.user.personalNIT)) {
      // Mostramos un mensaje de error si la validación falla
      this.utilsService.dialog('ERROR', 'Error', 'El campo personalNIT debe contener solo números y letras.');
      return; // No continuamos con el envío de datos si la validación falla
    }

    this.user.permissions = this.permissions || [];
    this.user.communicationChannel = this.communicationChannels || [];
    //this.user.accountRelation = [this.accountRelationData];
    console.log(this.user);

    if (this.form.valid) {
      this.loading = true;
      const role = this.roleList.find((x) => x.id === Number(this.user.roleId));
      this.user.roles = [role!.key];
      this.userID > 0 ? this.editUser() : this.createUser();
      this.loading = false;
    } else {
      this.form.control.markAllAsTouched();
      this.utilsService.dialog('ERROR', 'Error', '¡Llena los campos obligatorios!');
    }
  }

  createUser() {
    this.user.password = 'Aplus123'; // "pass123";
    this.loading = true;
    this.userService.createUser(this.user).then(
      (response) => {
        console.log(response);
        this.utilsService.dialog('SUCCESS', 'Éxito', response.message.description, '/admin/users/admin-usuarios');
      },
      (error) => {
        console.log(error);
        this.utilsService.dialog('ERROR', 'Error', error.error.message.description);
      }
    ).finally(() => {
      this.loading = false;
    });
  }

